let form = document.getElementById("form")
form.setAttribute('novalidate', 'novalidate');



const validPhone = () => {

    let rules = /\d{1}\s[\(]\d{3}[\)]\s\d{3}[\-]\d{2}[\-]\d{2}/;
    let userPhone = document.getElementById('tel').value;
    let valid = rules.test(userPhone);
    if (valid) 
        return valid;

}

const validPassword = () => {
    let rules = /(?=.*\d)(?=.*[a-z]).{5,}/;
    let userPassword = document.getElementById('password').value;
    var valid = rules.test(userPassword);
    if (valid)
        return valid;


}







